<template>
  <VTypography class="text-counter" font-style="text-3"
    >{{ count }}/{{ maxCount }}</VTypography
  >
</template>

<script>
import VTypography from "@/components/ui/Typography";

export default {
  name: "TextCounter",
  components: { VTypography },
  props: {
    text: String,
    maxCount: Number,
    rootClass: String,
  },
  computed: {
    count() {
      return this.text?.length || 0;
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.text-counter {
  color: $gray4;
}
</style>
