<template>
  <div
    class="v-language"
    :class="rootClasses"
    @mouseenter="showMenu = true"
    @mouseleave="showMenu = false"
  >
    <VTypography font-style="text-2" class="v-language__title">{{
      modelValue
    }}</VTypography>

    <div v-if="showMenu" class="v-language__menu">
      <VButton
        :key="language"
        v-for="language in list"
        @click="clickItem(language)"
        class="v-language__menu-item"
      >
        <VTypography font-style="text-2" class="v-language__menu-title">{{
          language
        }}</VTypography>
      </VButton>
    </div>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import VButton from "@/components/buttons/VButton";

export default {
  name: "VLanguage",
  components: { VButton, VTypography },
  data() {
    return {
      showMenu: false,
    };
  },
  props: {
    modelValue: String,
    languages: {
      type: Array,
      required: true,
      default: () => [],
    },
  },
  methods: {
    clickItem(language) {
      this.showMenu = false;
      this.$emit("update:modelValue", language);
    },
  },
  computed: {
    rootClasses() {
      return {
        "v-language--active": this.showMenu,
      };
    },
    list() {
      return this.languages.filter((x) => x !== this.modelValue);
    },
  },
};
</script>

