@import "~@/assets/styles/variables.scss";

.v-language {
  width: 32px;
  height: 32px;
  display: flex;
  cursor: pointer;
  user-select: none;
  position: relative;
  align-items: center;
  box-sizing: content-box;
  justify-content: center;
  border: 2px solid $gray1;
}

.v-language--active {
  background-color: $gray1;

  & > .v-language__title {
    color: $white;
  }
}

.v-language__menu {
  top: 32px;
  display: flex;
  position: absolute;
  flex-direction: column;
  border: 2px solid $gray1;
}

.v-language__menu-item {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.v-language__title {
  text-transform: uppercase;
}

.v-language__menu-title {
  text-transform: uppercase;
}
</style>
