import { sleep } from "@/utils/SystemUtils";

export const technologyModule = {
  namespaced: true,
  state: {
    files: [],
  },
  getters: {
    getFiles(state) {
      return state.files;
    },
  },
  mutations: {
    addFiles(state, files) {
      const list = state.files.slice();

      state.files = list.concat(files);
    },
    removeFile(state, index) {
      const list = state.files.slice();

      list.splice(index, 1);

      state.files = list;
    },
  },
  actions: {
    async uploadFiles(store, files) {
      const restFiles = Object.values(files)
        .filter((x) => x.size < 104857600)
        .map((x) => ({ name: x.name }));

      try {
        // Имитация запроса на сервер
        await sleep(2000);

        store.commit("addFiles", restFiles);
      } catch (e) {
        return e;
      }
    },
  },
};
