<template>
  <VButton
    :class="rootClasses"
    class="action-button"
    @mouseenter="hover = true"
    @mouseleave="hover = false"
  >
    <div v-if="hasLeftIcon" class="action-button__left-icon-container">
      <slot name="leftIcon" :hover="hover"></slot>
    </div>

    <VTypography
      font-weight="heavy"
      :class="titleClasses"
      class="action-button__title"
    >
      <slot></slot>
    </VTypography>

    <div v-if="hasRightIcon" class="action-button__right-icon-container">
      <slot name="rightIcon" :hover="hover"></slot>
    </div>
  </VButton>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";

export default {
  name: "ActionButton",
  components: { VTypography, VButton },
  props: {
    buttonType: {
      type: String,
      default: "primary",
      validator(value) {
        return ["primary", "secondary", "outline"].includes(value);
      },
    },
  },
  data() {
    return {
      hover: false,
    };
  },
  computed: {
    hasRightIcon() {
      return !!this.$slots.rightIcon;
    },
    hasLeftIcon() {
      return !!this.$slots.leftIcon;
    },
    rootClasses() {
      return {
        "action-button--hover": this.hover || this.buttonType === "secondary",
        "action-button--outline": this.hover || this.buttonType === "outline",
      };
    },
    titleClasses() {
      return {
        "action-button__title--hover":
          this.hover || this.buttonType === "secondary",
        "action-button__title--outline":
          this.hover || this.buttonType === "outline",
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.action-button {
  height: 45px;
  background-color: $gray1;
}

.action-button--outline {
  border: 2px solid $green;
}

.action-button--hover {
  background-color: $green;
}

.action-button__title {
  color: $white;
}

.action-button__title--outline {
  color: $green;
}

.action-button__title--hover {
  color: $gray1;
}

.action-button__right-icon-container {
  margin-left: 10px;
}

.action-button__left-icon-container {
  margin-right: 10px;
}
</style>
