<template>
  <span class="v-typography" :class="[fontStyleClasses, fontWeightClasses]">
    <slot></slot>
  </span>
</template>

<script>
export default {
  name: "VTypography",
  props: {
    fontStyle: {
      type: String,
      default: "text-1",
      validator(value) {
        return [
          "text-1",
          "text-2",
          "text-3",
          "header-1",
          "header-2",
          "header-3",
        ].includes(value);
      },
    },
    fontWeight: {
      type: String,
      default: "demi",
      validator(value) {
        return ["demi", "medium", "heavy"].includes(value);
      },
    },
  },
  computed: {
    fontStyleClasses() {
      return {
        "v-typography--text-1": this.fontStyle === "text-1",
        "v-typography--text-2": this.fontStyle === "text-2",
        "v-typography--text-3": this.fontStyle === "text-3",
        "v-typography--header-1": this.fontStyle === "header-1",
        "v-typography--header-2": this.fontStyle === "header-2",
        "v-typography--header-3": this.fontStyle === "header-3",
      };
    },
    fontWeightClasses() {
      return {
        "v-typography--demi": this.fontWeight === "demi",
        "v-typography--medium": this.fontWeight === "medium",
        "v-typography--heavy": this.fontWeight === "heavy",
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.v-typography--text-1 {
  font-size: 16px;
  line-height: 22px;
}

.v-typography--text-2 {
  font-size: 14px;
  line-height: 20px;
}

.v-typography--text-3 {
  font-size: 12px;
  line-height: 16px;
}

.v-typography--header-1 {
  font-size: 20px;
  line-height: 24px;
}

.v-typography--header-2 {
  font-size: 30px;
  line-height: 35px;
}

.v-typography--header-3 {
  font-size: 40px;
  line-height: 45px;
}

.v-typography--demi {
  font-weight: $font-weight-demi;
}

.v-typography--medium {
  font-weight: $font-weight-medium;
}

.v-typography--heavy {
  font-weight: $font-weight-heavy;
}
</style>
