<template>
  <div class="text-input" :class="rootClass">
    <TypographyWithInfo
      v-if="!!label"
      font-weight="medium"
      :class="labelClasses"
      class="text-input__label"
    >
      <template v-slot:default>
        {{ label }}
      </template>

      <template v-slot:popover>
        <slot name="popover"></slot>
      </template>
    </TypographyWithInfo>

    <div
      :class="containerClasses"
      @mouseenter="hover = true"
      @mouseleave="hover = false"
      class="text-input__container"
    >
      <slot name="startAdornment" v-if="hasStartAdornment" />

      <input
        ref="text-input"
        v-bind="$attrs"
        :value="modelValue"
        :disabled="disabled"
        @blur="focus = false"
        @focus="focus = true"
        @click="onFieldClick"
        :class="fieldClasses"
        :maxlength="maxlength"
        :aria-multiline="true"
        class="text-input__field"
        @input="$emit('update:modelValue', $event.target.value)"
      />

      <div class="text-input__end-adornment-container">
        <slot name="endAdornment" v-if="hasEndAdornment" />
      </div>

      <TextCounter
        :text="modelValue"
        :class="counterClasses"
        :max-count="maxlength"
        class="text-input__counter"
        v-if="!!maxlength && (!!modelValue || focus) && !disabled"
      />
    </div>

    <VTypography font-style="text-2" v-if="!!error" class="text-input__error">{{
      error
    }}</VTypography>
  </div>
</template>

<script>
import TextCounter from "@/components/ui/TextCounter";
import VTypography from "@/components/ui/Typography";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "TextInput",
  inheritAttrs: false,
  components: {
    VTypography,
    TextCounter,
    TypographyWithInfo,
  },
  props: {
    disabled: Boolean,
    label: String,
    error: String,
    rootClass: String,
    modelValue: String,
    multiline: {
      type: Boolean,
      default: true,
    },
    maxlength: {
      type: Number,
      default: 100,
    },
    inputType: {
      type: String,
      default: "default",
      validator(value) {
        return ["default", "contrast"].includes(value);
      },
    },
  },
  data() {
    return {
      hover: false,
      focus: false,
    };
  },
  computed: {
    hasEndAdornment() {
      return !!this.$slots.endAdornment;
    },
    hasStartAdornment() {
      return !!this.$slots.startAdornment;
    },
    containerClasses() {
      return {
        "text-input__container--focus": this.focus,
        "text-input__container--error": !!this.error,
        "text-input__container--disabled": this.disabled,
        "text-input__container--contrast": this.inputType === "contrast",
        "text-input__container--hover":
          ((!this.focus && this.hover) || (!this.focus && this.modelValue)) &&
          !this.disabled,
      };
    },
    fieldClasses() {
      return {
        "text-input__field--contrast": this.inputType === "contrast",
      };
    },
    labelClasses() {
      return {
        "text-input__label--contrast": this.inputType === "contrast",
      };
    },
    counterClasses() {
      return {
        "text-input__counter--contract": this.inputType === "contrast",
      };
    },
  },
  methods: {
    onFieldClick(event) {
      event.stopPropagation();
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.text-input {
  display: flex;
  flex-direction: column;
}

.text-input__info-popover {
  margin-left: 10px;
}

.text-input__container {
  @include transition(border-color);
  height: 45px;
  display: flex;
  border: 2px solid $gray4;
}

.text-input__field {
  padding: 0;
  flex-grow: 1;
  border: none;
  outline: none;
  margin: 0 13px;
  flex-shrink: 1;
  background-color: transparent;
  font-weight: $font-weight-medium;
}

.text-input__field--contrast {
  color: $white;
}

.text-input__label {
  margin-bottom: 10px;
}

.text-input__label--contrast {
  color: $white;
}

.text-input__container--hover {
  border-color: $gray1;
}

.text-input__container--contrast {
  border-color: $white;
}

.text-input__container--focus {
  border-color: $green;
}

.text-input__container--error {
  border-color: $red;
  margin-bottom: 5px;
}

.text-input__container--disabled {
  border-color: $gray5;
  background-color: $gray5;
}

.text-input__counter {
  align-self: center;
  margin-right: 13px;
}

.text-input__counter--contract {
  color: $gray2;
}

.text-input__end-adornment-container {
  display: flex;
  margin-right: 13px;
  align-items: center;
  justify-content: center;
}

.text-input__error {
  color: $red;
}
</style>
