<template>
  <TextInput root-class="password-input" :type="fieldType">
    <template v-slot:endAdornment>
      <EyeButton :opened="showPassword" @click="showPassword = !showPassword" />
    </template>
  </TextInput>
</template>

<script>
import TextInput from "@/components/ui/TextInput";
import EyeButton from "@/components/buttons/EyeButton";

export default {
  name: "PasswordInput",
  components: { EyeButton, TextInput },
  data() {
    return {
      hover: false,
      showPassword: false,
    };
  },
  computed: {
    fieldType() {
      return this.showPassword ? "text" : "password";
    },
  },
};
</script>
