export const generalFormModule = {
  namespaced: true,
  state: {
    russianInformation: "",
    englishInformation: "",
  },
  mutations: {
    changeRussianInformation(state, value) {
      state.russianInformation = value;
    },
    changeEnglishInformation(state, value) {
      state.englishInformation = value;
    },
  },
};
