<template>
  <div class="v-popover" v-click-outside="onClickOutside">
    <VButton
      @click="onClick"
      @mouseenter="onMouseEnter"
      @mouseleave="onMouseLeave"
      class="v-popover__icon-container"
    >
      <div ref="iconContainer" class="v-popover__icon-content">
        <slot :active="show" name="icon"></slot>
      </div>
    </VButton>

    <div
      v-if="show"
      ref="popover"
      :class="popoverClass"
      class="v-popover__popover"
      :style="{
        top: `${top}px`,
        left: `${left}px`,
      }"
    >
      <slot name="popover"></slot>
    </div>
  </div>
</template>

<script>
import VButton from "@/components/buttons/VButton";

export default {
  name: "VPopover",
  components: { VButton },
  emits: {
    click: Function,
  },
  props: {
    color: String,
    disabled: Boolean,
    popoverClass: String,
    triggerType: {
      type: String,
      default: "click",
      validator(value) {
        return ["click", "hover"].includes(value);
      },
    },
    position: {
      type: String,
      default: "top-right",
      validator(value) {
        return [
          "top-left",
          "top-center",
          "top-right",
          "middle-center",
          "center",
          "middle-right",
          "bottom-left",
          "bottom-center",
          "bottom-right",
        ].includes(value);
      },
    },
  },
  data() {
    return {
      top: 0,
      left: 0,
      show: false,
    };
  },
  updated() {
    if (this.show) {
      const popover = this.$refs.popover;
      const iconContainer = this.$refs.iconContainer;

      const popoverWidth = popover?.clientWidth;
      const popoverHeight = popover?.clientHeight;
      const iconWidth = iconContainer?.clientWidth;
      const iconHeight = iconContainer?.clientHeight;

      const bottom = iconHeight + 5;
      const top = -(popoverHeight + 5);

      const left = -(popoverWidth + 5);
      const right = iconWidth + 5;

      const verticalCenter = -(popoverHeight / 2 - iconHeight / 2);
      const horizontalCenter = -(popoverWidth / 2 - iconWidth / 2);

      switch (this.position) {
        case "top-left":
          this.top = top;
          this.left = left;

          break;

        case "top-center":
          this.top = top;
          this.left = horizontalCenter;

          break;

        case "top-right":
          this.top = top;
          this.left = right;

          break;

        case "middle-left":
          this.left = left;
          this.top = verticalCenter;

          break;

        case "center":
          this.top = verticalCenter;
          this.left = horizontalCenter;

          break;

        case "middle-right":
          this.left = right;
          this.top = verticalCenter;

          break;

        case "bottom-left":
          this.left = left;
          this.top = bottom;

          break;

        case "bottom-center":
          this.left = horizontalCenter;
          this.top = bottom;

          break;

        case "bottom-right":
          this.left = right;
          this.top = bottom;

          break;
      }
    }
  },
  methods: {
    onMouseEnter() {
      if (this.triggerType === "hover" && !this.disabled) {
        this.show = true;
      }
    },
    onMouseLeave() {
      if (this.triggerType === "hover" && !this.disabled) {
        this.show = false;
      }
    },
    onClick() {
      if (this.triggerType === "click" && !this.disabled) {
        this.show = !this.show;
      }

      if (!this.disabled) {
        this.$emit("click");
      }
    },
    onClickOutside() {
      if (this.show) {
        this.show = false;
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.v-popover {
  user-select: none;
  position: relative;
}

.v-popover__popover {
  z-index: 1000;
  position: absolute;
  background-color: $gray1;
}

.v-popover__popover--show {
  display: flex;
}

.v-popover__icon-container {
  display: flex;
  cursor: pointer;
}

.v-popover__icon-content {
  display: flex;
}
</style>
