<template>
  <div class="typography-with-info">
    <div class="typography-with-info__container" :class="containerClasses">
      <VTypography
        :font-style="fontStyle"
        :font-weight="fontWeight"
        class="typography-with-info__label"
      >
        <slot></slot>
      </VTypography>
      <VPopover
        v-if="hasPopover"
        :position="popoverPosition"
        class="typography-with-info__popover"
      >
        <template v-slot:icon="slotProps">
          <InfoIcon :active="slotProps.active" :size="16" />
        </template>

        <template v-slot:popover>
          <slot name="popover"></slot>
        </template>
      </VPopover>
    </div>
  </div>
</template>

<script>
import VPopover from "@/components/popover/Popover";
import InfoIcon from "@/components/icons/InfoIcon";
import VTypography from "@/components/ui/Typography";

export default {
  name: "TypographyWithInfo",
  components: { VTypography, InfoIcon, VPopover },
  props: {
    fontStyle: String,
    fontWeight: String,
    popoverPosition: String,
    position: {
      type: String,
      default: "right",
      validate(value) {
        return ["left", "right"].includes(value);
      },
    },
  },
  computed: {
    hasPopover() {
      return !!this.$slots.popover;
    },
    containerClasses() {
      return {
        "typography-with-info__container--reverse": this.position === "left",
      };
    },
  },
};
</script>

<style scoped lang="scss">
.typography-with-info {
  display: flex;
}

.typography-with-info__container {
  display: flex;
}

.typography-with-info__container--reverse {
  align-items: center;
  flex-direction: row-reverse;
}

.typography-with-info__popover {
  margin: 0 10px;
}
</style>
