<template>
  <div class="login-form">
    <PasswordInput
      :error="error"
      :maxlength="8"
      v-model="password"
      root-class="login-form__password-input"
    />

    <ActionButton class="login-form__submit-button" @click="onSubmit"
      >Войти</ActionButton
    >

    <VTypography
      font-style="text-2"
      font-weight="medium"
      class="login-form__info-text"
    >
      Пароль был отправлен на почту в момент создания формы раскрытия. Если вы
      не получили пароль для входа на страницу, напишите на почту
      <a target="_blank" class="login-form__link" href="mailto:kto@skoltech.ru"
        >kto@skoltech.ru</a
      >
    </VTypography>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import PasswordInput from "@/components/ui/PasswordInput";
import ActionButton from "@/components/buttons/ActionButton";

export default {
  name: "LoginForm",
  components: {
    VTypography,
    ActionButton,
    PasswordInput,
  },
  emits: {
    submit: Function,
  },
  data() {
    return {
      type: "",
      error: "",
      password: "",
    };
  },
  methods: {
    onSubmit() {
      this.error = "";

      if (this.password.length < 8) {
        this.error = "Пароль должен состоять из 8 символов";
      } else {
        this.$emit("submit");
      }
    },
  },
};
</script>

