<template>
  <VLanguage v-model="language" :languages="['ru', 'en']" />
</template>

<script>
import VLanguage from "@/components/ui/Language";
import { mapMutations } from "vuex";

export default {
  name: "AppLanguage",
  components: { VLanguage },
  methods: {
    ...mapMutations("application", ["changeApplicationLanguage"]),
  },
  computed: {
    language: {
      set(language) {
        this.changeApplicationLanguage(language);
      },
      get() {
        return this.$store.state.application.language;
      },
    },
  },
};
</script>
